/* src/WorkExperience.css */
.work-experience {
    margin: 30px auto;
    max-width: 1000px;
    border-radius: 12px;
    background-color: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .work-experience:hover {
    transform: translateY(-2px);
    box-shadow: 0 8px 12px rgba(0, 0, 0, 0.15);
  }
  
  .experience {
    display: flex; /* Добавлено для горизонтального выравнивания */
    align-items: flex-start; /* Выравнивание по верхнему краю */
    margin-bottom: 25px;
    padding: 20px;
    border-radius: 8px;
    background-color: #f8fafc;
    transition: background-color 0.3s ease;
  }
  
  .experience:not(:last-child) {
    margin-bottom: 30px;
  }
  
  .experience:hover {
    background-color: #4b79a62b;
  }
  
  .experience h3 {
    color: #1e40af;
    font-size: 1.3rem;
    margin: 0 0 8px 0;
    font-weight: 1000;
  }
  
  .experience .company {
    color: #4a5568;
    font-size: 1.1rem;
    font-weight: 1000;
    display: block;
    margin-bottom: 12px;
  }
  
  .experience .duration {
    color: #718096;
    font-size: 0.9rem;
    margin-bottom: 15px;
    display: inline-block;
    padding: 4px 8px;
    background-color: #e2e8f0;
    border-radius: 4px;
  }
  
  .experience ul {
    list-style-type: none;
    padding-left: 20px;
    margin: 15px 0 0 0;
  }
  
  .experience li {
    color: #4a5568;
    margin-bottom: 8px;
    line-height: 1.6;
    position: relative;
    padding-left: 20px;
  }
  
  .experience li:before {
    content: "▹";
    color: #1e40af;
    position: absolute;
    left: 0;
    top: 1px;
  }
  
  .experience-content {
    flex: 1; /* Занимает оставшееся пространство */
    margin-right: 20px; /* Отступ от логотипа */
  }
  
  .experience-logo {
    display: flex;
    align-items: center; /* Центрирование логотипа по вертикали */
    justify-content: center; /* Центрирование логотипа по горизонтали */
  }
  
  .experience-logo img {
    padding: 30px;
    align-items: center; /* Центрирование логотипа по вертикали */
width: 150px;
    height: auto; /* Автоматическая высота */
  }
  
  @media (max-width: 100%) {
    .work-experience {
      margin: 20px;
      padding: 0px;
    }
  
    .experience {
      flex-direction: column; /* Вертикальное выравнивание на маленьких экранах */
      padding: 15px;
    }
  
    .experience-logo {
      margin-top: 15px; /* Отступ сверху для логотипа */
    }
  }
  